import Home from '../pages/Home.vue'
const routers = [
    {
        path: '/',
        name: 'homeApp',
        component : Home
    },
    {
        path:'/edu',
        name:'edu',
        component:() => import('../pages/eduPage/edu')
    },
    {
        path:'/pass',
        name:'pass',
        component:() => import('../pages/passPage/passPage')
    }
]

export default  routers


